import * as types from '../mutation-types.js'
import Api from '../../service/api.js'

// alert提示信息
export const showAlert = ({ dispatch }, data) => {
	dispatch(types.COMMON__SHOW_ALERT, data);
};



// 获取base data
export const getBaseData = ({ dispatch }) => {
  $.ajax({
      url: Api.get('getBaseData'),
      type: 'GET',
      dataType: 'json',
      async: false
    })
    .done(function(res) {
      if( res.code != 200 ){
        dispatch(types.COMMON__GET_BASE_DATA, null);
        return;
      }
      dispatch(types.COMMON__GET_BASE_DATA, res.data);
    });
};





