<!-- 入口列表 -->
<template>
<div id="entrance">
	微信
	<input type="file" id="uploadImg">
	<div class="btn btn-default"  @click="fnUploadFile">上传</div>
	<img :src="imagesUrl" alt="" style="width: 100%;">
</div>
</template>

<script>
import Api from '../../service/api.js'
import { getBaseData } from '../../vuex/actions/common.js'

export default {
	data() {
		return {
			paths: [], //面包屑导航数据
			path: '',
		imagesUrl: '',
		}
	},
	vuex: {
		getters: {
			baseData: state => state.common.baseData,
		},
		actions: {
			getBaseData,
		},
	},
	computed: {
		entranceList() {
			var channel_list = this.baseData.channel_list;
			var department_list = this.baseData.department_list;

			if (this.$route.path == '/index' && channel_list) {
				var temp = [];
				$.each(channel_list, function(index, val) {
					var data = $.extend(true, {}, val);
					data.id = val.channel_id;
					data.name = val.channel_name;
					temp.push(data);
				});
				this.paths = [{
					name: '入口',
					path: '/'
				}];
				return temp;
			} else if (this.$route.path == '/index_department' && department_list) {
				this.paths = [{
					name: '部类',
					path: '/'
				}];
				return department_list;
			}

			return [];
		},

		path() {
			return this.$route.path;
		},

	},
	methods: {
		fnUploadFile (ev){
			var $vm = this;
			var file = document.getElementById("uploadImg").files[0];  
		    if(!/image\/\w+/.test(file.type)){  
		        alert("看清楚，这个需要图片！");  
		        return false;  
		    }  
		    var reader = new FileReader();  
		    //将文件以Data URL形式读入页面  
		    reader.readAsDataURL(file); 
		    reader.onload=function(e){  
		        $vm.imagesUrl =  this.result;
		    }  
			debugger
		},
	},
	route: {
		activate: function(transition) {
			this.getBaseData();
			
			transition.next();
		},
		canReuse: false,
	},
	components: {
		'breadCrumb': require('../../components/bread_crumb.vue'),
		'entranceList': require('./entrance_list/index.vue'),
	}

}


</script>


<style>
</style>


